<template>
    <div class="contact " id="contact" data-target="contact">
        <div class="container">
            <div class="contact__body">
                <li class="contact__body-title">
                    <h2 class="contact__body-title-h">{{ $t('сontactme') }}</h2>
                </li>
                <p class="contact__body-p">{{ $t('address') }}</p>
                <div class="contact__body-info">
                    <div class="contact__body-info-email">
                        <img src="@/assets/images/email.svg" alt="">
                        <p class="contact__body-info-email-p">rromanrustamov@gmail.com</p>
                        <button @click="copyToClipboard('rromanrustamov@gmail.com')">
                            <img src="@/assets/images/copy.svg" alt="">
                        </button>
                    </div>
                    <div class="contact__body-info-tel">
                        <img src="@/assets/images/tel.svg" alt="">
                        <p class="contact__body-info-tel-p">+998(90) 353-03-19</p>
                        <button @click="copyToClipboard('+988(90)3530319')">
                            <img src="@/assets/images/copy.svg" alt="">
                        </button>
                    </div>

                </div>
            </div>
        </div>
    </div>
    <footer class="footer">
        <h2 class="footer__h">2023 | Designed and coded with by Roma</h2>
    </footer>
</template>


<script>
export default {
    methods: {
        copyToClipboard(text) {
            const textarea = document.createElement("textarea");
            textarea.value = text;
            document.body.appendChild(textarea);
            textarea.select();
            document.execCommand("copy");
            document.body.removeChild(textarea);

            this.showAlert(`Text "${text}" copied.`);
        },
        showAlert(message) {
            const alertDiv = document.createElement("div");
            alertDiv.classList.add("alert");
            alertDiv.innerText = message;
            document.body.appendChild(alertDiv);

            setTimeout(() => {
                alertDiv.style.opacity = "0";
                setTimeout(() => {
                    document.body.removeChild(alertDiv);
                }, 500);
            }, 2000);
        },
    },

};


</script>


<style lang="scss" scoped></style>

