import {
    createApp
} from 'vue'
import App from './App.vue'
import '@/assets/scss/main.scss'
import {
    languages
} from "./i18n";
import {
    createI18n
} from 'vue-i18n'
const localeStorageLang = localStorage.lang

const i18n = createI18n({
    legacy: true,
    locale: localeStorageLang || 'us',
    messages: languages

})

const app = createApp(App)
app.use(i18n)
app.mount('#app')