<template>
    <header class="header">
        <div class="header__body container">
            <a href="#" class="header__body-logo">
                <img src="@/assets/images/logo.svg" alt="">
            </a>
            <div class="header__body-burger" @click="view = !view">
                <img src="@/assets/images/menu.svg" alt="" v-if="view">
                <img src="@/assets/images/cross.svg" alt="" v-else>
            </div>
            <nav class="header__body-menu" :class="{ active: !view }">
                <ul class="header__body-menu-list">
                    <li @click="scrollToBlock('summary')" class="header__body-menu-list-link">{{ $t('home') }}</li>
                    <li @click="scrollToBlock('about')" class="header__body-menu-list-link">{{ $t('about') }}</li>
                    <li @click="scrollToBlock('scills')" class="header__body-menu-list-link">{{ $t('skills') }}</li>
                    <li @click="scrollToBlock('projects')" class="header__body-menu-list-link">{{ $t('project') }}</li>
                    <li @click="scrollToBlock('contact')" class="header__body-menu-list-link">{{ $t('сontact') }}</li>
                    <div class="header__body-menu-list-strip"></div>
                    <div class="header__body-menu-list-theme">
                        <span class="header__body-menu-list-theme-desc">{{ $t('theme') }}</span>
                        <img src='@/assets/images/lighttheme.svg' alt="">
                    </div>
                    <button class="header__body-menu-list-lang" @click="switchLang">{{ $i18n.locale }}</button>
                </ul>
            </nav>
        </div>

    </header>
</template>

<script>
export default {
    data() {
        return {
            view: true
        };
    },

    methods: {
        switchLang() {
            this.$i18n.locale == 'us' ? this.$i18n.locale = 'ru' : this.$i18n.locale = 'us'
            localStorage.lang = this.$i18n.locale
        },
        scrollToBlock(targetid) {
            const element = document.querySelector(`[data-target="${targetid}"]`)
            if (element) {
                element.scrollIntoView({ behavior: 'smooth' })
            }
        },

    },



}




</script>

<style lang="scss" scoped></style>